.form__input {
    padding: 9px 5px 9px;
    border: none;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(0, 0, 0, .2);
    width: 358px;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    box-sizing: border-box;
}

.form__input:last-child {
    margin-bottom: 0;
}

.form__input::placeholder {
    color: #C4C4C4;
}

@media (max-width: 650px) {
    .form__input {
        width: 238px;
    }
}