.popup__close {
    position: absolute;
    top: -40px;
    right: -40px;
    width: 32px;
    height: 32px;
    color: #fff;
    border: none;
    background-color: transparent;
    background-image: url(../../../images/close-Icon.svg);
    background-repeat: no-repeat;
    background-position: center;
    font-size: 0;
    padding: 0;
    cursor: pointer;
    transition: opacity .2s linear;
}

.popup__close:hover {
    opacity: .6;
}


@media (max-width: 650px) {
    .popup__close {
        width: 20px;
        height: 20px;
        right: 0;
        top: -35px;
        background-size: 20px 20px;
    }
}